import {Select} from "antd";
import {useSelector} from "react-redux";

export const SubmissionPeriod = ({onChangeTop}) => {

  const options = [
    { value: '0', label: '' },
    { value: '1', label: ' ' },
    { value: '2', label: ' i' },
    { value: '3', label: '  i' },
    { value: '4', label: ' ' },
    { value: '5', label: ' i' },
    { value: '6', label: ' 3 ii' },
    { value: '8', label: '  3 ' },
    { value: '9', label: ' 6 ' },
    { value: '10', label: ' 2 i' },
    { value: '11', label: '  ' },
    { value: '14', label: ' 12 ' },
  ]

  const top = useSelector(state => state.filters.filteringParams.top).toString()

  const handleChange = (value) => {
    console.log(`selected ${value}`);
    onChangeTop(value)
  };


  return (
    <Select
      value={top}
      style={{ width:'100%'}}
      onChange={handleChange}
      options={options}
    />
  )
}